import prettyMilliseconds from 'pretty-ms';

export const extend = (a, b) => {
  return Object.assign({}, a, b);
};

export const addStatusMixin = (block, status) => {
  if (status === `InProgress`) {
    return `${block}--status-in-progress`;
  };

  return `${block}--status-${status.toLowerCase()}`;
};

export const formatDate = (date) => {
  const locale = (navigator.languages && navigator.languages.length) ? navigator.languages[0] : navigator.language;

  const format = {
    day: `numeric`,
    month: `short`,
    hour: `numeric`,
    minute: `numeric`
  };

  return new Date(date).toLocaleDateString(locale, format);
};

export const formatDuration = (duration) => prettyMilliseconds(duration);

export const delay = (ms) => new Promise((resolve) => {
  setTimeout(resolve, ms);
});
