import React from 'react';
import PropTypes from 'prop-types';
import IconSuccess from '../Icons/IconSuccess/IconSuccess';
import IconCross from '../Icons/IconCross/IconCross';
import IconWaiting from '../Icons/IconWaiting/IconWaiting';

const BuildStatus = ({status}) => {
  switch (status) {
    case `Success`:
      return <IconSuccess className="build-card__status icon--status" width="14" height="14"/>;
    case `Fail`:
    case `Canceled`:
      return <IconCross className="build-card__status icon--status" width="14" height="14"/>;
    case `Waiting`:
    case `InProgress`:
      return <IconWaiting className="build-card__status icon--status" width="14" height="14"/>;
  }

  return null;
};

BuildStatus.propTypes = {
  status: PropTypes.oneOf([`Waiting`, `InProgress`, `Success`, `Fail`, `Canceled`])
};

export default BuildStatus;
