import React from 'react';
import PropTypes from 'prop-types';

import {connect} from 'react-redux'

import history from '../../history';
import {AppRoutes} from '../../const';
import {BrowserRouter, Route, Switch} from 'react-router-dom';

import PageStartScreen from '../Pages/PageStartScreen/PageStartScreen';
import PageSettings from '../Pages/PageSettings/PageSettings';
import PageBuildDetails from '../Pages/PageBuildDetails/PageBuildDetails';
import PageBuildHistory from '../Pages/PageBuildHistory/PageBuildHistory';

import {Operation} from '../../reducer/settings/reducer';
import {getSettings, getLoadedFlag} from '../../reducer/settings/selectors';

class App extends React.PureComponent {
  constructor(props) {
    super(props);
  }

  componentWillMount () {
    const {
      loadSettings,
    } = this.props;

    loadSettings();
  }

  render() {
    const {
      loaded,
      settings
    } = this.props;

    // Need flag to avoid glitching of the start screen while data is not loaded
    if (!loaded) {
      return null;
    }

    return <BrowserRouter history={history}>
      <Switch>
        <Route path={AppRoutes.HOME} exact render={() => (settings ? <PageBuildHistory/> : <PageStartScreen/>)}/>
        <Route path="/dev-history" exact component={PageBuildHistory}/>
        <Route path="/dev-start-screen" exact component={PageStartScreen}/>
        <Route path={AppRoutes.SETTINGS} exact component={PageSettings}/>
        <Route path={AppRoutes.BUILD + `/:number`} exact component={PageBuildDetails}/>
      </Switch>
    </BrowserRouter>;
  }
};

const mapStateToProps = (state) => ({
  settings: getSettings(state),
  loaded: getLoadedFlag(state),
});

const mapDispatchToProps = (dispatch) => ({
  loadSettings: () => {
    dispatch(Operation.loadSettings());
  }
});

App.propTypes = {
  loaded: PropTypes.bool.isRequired
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
