import {extend, delay} from '../../utils';
import mockedBuilds from '../../mock/builds';

const initialState = {
  builds: []
};

const ActionType = {
  SET_BUILDS: `SET_BUILDS`
};

const ActionCreator = {
  setBuilds: (builds) => ({
    type: ActionType.SET_BUILDS,
    payload: builds
  })
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case ActionType.SET_BUILDS:
      return extend(state, {
        builds: action.payload
      });
  }

  return state;
};

const Operation = {
  loadBuilds: () => (dispatch, getState, api) => {
    return delay(250).then(() => dispatch(ActionCreator.setBuilds(mockedBuilds)));
    // return api.get(`/builds`).then((data) => dispatch(ActionCreator.setBuilds(data)));
  }
};

export {
  ActionType,
  ActionCreator,
  reducer,
  Operation
};
