import React from 'react';

import {connect} from 'react-redux';

import {AppRoutes} from '../../../const';
import {Link} from 'react-router-dom';

import BuildLog from '../../BuildLog/BuildLog';
import BuildCard from '../../BuildCard/BuildCard';
import Footer from '../../Footer/Footer';
import IconSettings from '../../Icons/IconSettings/IconSettings';
import IconRebuild from '../../Icons/IconRebuild/IconRebuild';

import {ActionCreator, Operation as BuildOperation} from '../../../reducer/build/reducer';
import {getBuild, getLog} from '../../../reducer/build/selectors';
import {Operation as SettingsOperation} from '../../../reducer/settings/reducer';
import {getSettings} from '../../../reducer/settings/selectors';

class PageBuildDetails extends React.PureComponent {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    const {
      match,
      settings,
      loadSettings,
      loadData,
    } = this.props;

    if (!settings) {
      loadSettings();
    }

    const buildNumber = Number(match.params.number);
    loadData(buildNumber);
  }

  componentWillUnmount() {
    const {
      resetData
    } = this.props;

    resetData();
  }

  render() {
    const {
      settings,
      build,
      log,
      handleSubmit
    } = this.props;

    if (!settings || !build) {
      return null;
    }

    return <React.Fragment>
      <header className="header container">
        <h1 className="visually-hidden">School CI Server</h1>
        <h2 className="header__heading heading heading--h2">{settings.repoName}</h2>
        <form action="/" method="post" onSubmit={handleSubmit}>
          <input type="hidden" name="commitHash" value={build.commitHash}/>
          <button className="btn btn--small btn--with-icon" type="submit">
            <IconRebuild className="btn__icon icon--btn" width="12" height="12"/>
            <span className="btn__text">Rebuild</span>
          </button>
        </form>
        <Link className="btn btn--small btn--with-icon btn--compact" to={AppRoutes.SETTINGS} title="Settings">
          <IconSettings className="btn__icon icon--btn" width="12" height="12"/>
          <span className="btn__text">Settings</span>
        </Link>
      </header>
      <main className="main main--build-details container">
        <BuildCard build={build}/>
        <BuildLog log={log}/>
      </main>
      <Footer/>
    </React.Fragment>;
  }
};

const mapStateToProps = (state) => ({
  settings: getSettings(state),
  build: getBuild(state),
  log: getLog(state),
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  loadSettings: () => {
    dispatch(SettingsOperation.loadSettings());
  },
  loadData: (buildNumber) => {
    dispatch(BuildOperation.loadBuild(buildNumber));
    dispatch(BuildOperation.loadLog(buildNumber));
  },
  resetData: () => {
    dispatch(ActionCreator.setBuild(null));
    dispatch(ActionCreator.setLog(null));
  },
  handleSubmit: (evt) => {
    evt.preventDefault();

    /*dispatch(Operation.addBuild({
      commitHash: evt.target.commitHash.value,
    }))
      .then((build) => ownProps.history.push(AppRoutes.BUILD + `/` + build.buildNumber));*/
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(PageBuildDetails);
