import React from 'react';
import PropTypes from 'prop-types';

import {addStatusMixin, formatDate, formatDuration} from '../../utils';

import BuildStatus from '../BuildStatus/BuildStatus';
import IconCalendar from '../Icons/IconCalendar/IconCalendar';
import IconWatch from '../Icons/IconWatch/IconWatch';
import IconCommit from '../Icons/IconCommit/IconCommit';
import IconUser from '../Icons/IconUser/IconUser';

const BuildCard = (props) => {
  const {
    build,
    isCompact = false
  } = props;

  if (!build) {
    return null;
  }

  return <section className={`build-card ` + (isCompact ? `build-card--compact ` : ``) + addStatusMixin(`build-card`, build.status)}>
    <div className="build-card__main">
      <BuildStatus status={build.status}/>
      <h3 className="build-card__issue heading">
        <span className="build-card__issue-number">#{build.buildNumber}</span>&nbsp;
        <span className="build-card__issue-comment">{build.commitMessage}</span>
      </h3>
      <div className="build-card__commit icon-wrapper">
        <IconCommit width="16" height="8"/>
        <span className="build-card__commit-branch">{build.branchName}</span>&nbsp;
        <span className="build-card__commit-hash">{build.commitHash}</span>
      </div>
      <div className="build-card__author icon-wrapper">
        <IconUser width="13" height="14"/>
        {build.authorName}
      </div>
    </div>
    <div className="build-card__time">
      <span className="build-card__time-start icon-wrapper">
        <IconCalendar width="14" height="16"/>
        {formatDate(build.start)}
      </span>
      <span className="build-card__time-duration icon-wrapper">
        <IconWatch width="14" height="16"/>
        {formatDuration(build.duration)}
      </span>
    </div>
  </section>;
};

BuildCard.propTypes = {
  build: PropTypes.exact({
    id: PropTypes.string.isRequired,
    configurationId: PropTypes.string.isRequired,
    buildNumber: PropTypes.number.isRequired,
    commitMessage: PropTypes.string.isRequired,
    commitHash: PropTypes.string.isRequired,
    branchName: PropTypes.string.isRequired,
    authorName: PropTypes.string.isRequired,
    status: PropTypes.oneOf([`Waiting`, `InProgress`, `Success`, `Fail`, `Canceled`]),
    start: PropTypes.string,
    duration: PropTypes.number
  }),
  isCompact: PropTypes.bool
};

export default BuildCard;
