import {extend, delay} from '../../utils';

const initialState = {
  settings: null,
  loaded: false // we need to check if there are settings on backend, they can be empty
};

const ActionType = {
  SET_SETTINGS: `SET_SETTINGS`
};

const ActionCreator = {
  setSettings: (settings) => ({
    type: ActionType.SET_SETTINGS,
    payload: settings
  }),
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case ActionType.SET_SETTINGS:
      return extend(state, {
        settings: action.payload,
        loaded: true
      });
  }

  return state;
};

const Operation = {
  loadSettings: () => (dispatch, getState, api) => {
    return api.get(`/settings`)
      .then((res) => dispatch(ActionCreator.setSettings(res.data)));
  },
  saveSettings: (settings) => (dispatch, getState, api) => {
    return delay(3000).then(() => {
      return api.post(`/settings`, {
        repoName: settings.repoName,
        buildCommand: settings.buildCommand,
        mainBranch: settings.mainBranch,
        period: settings.period
      })
        .then((res) => dispatch(ActionCreator.setSettings(res.data)));
    });
  }
};

export {
  ActionType,
  ActionCreator,
  reducer,
  Operation
};
